import React, { FC, useRef, useEffect} from 'react';
import '../../../styles/about/TeacherSlider.scss';
import { useAppData } from '../../../context/AppContext';

const quoteImages = require.context('../../../assets/aboutUs/quote', true, /\.svg$/);
const quotes = quoteImages.keys().map((path) => quoteImages(path));	
const bannerImages = require.context('../../../assets/aboutUs/banner', true, /\.png$/);
const banners = bannerImages.keys().map((path) => bannerImages(path));	

const TeacherSlider: FC = () => {	
	let counter = 1;
	const teacherSliderInfos = useAppData('teacherSliderInfos');
	const bgSetting = useAppData('bgSetting');
	const slider = useRef<HTMLDivElement>(null);
	const dot = useRef<HTMLDivElement[]>([]);
	const nextSlide = () => {
		if (slider.current) {	
			for (let i = 0; i < teacherSliderInfos.length; i++) {
				if (i === counter % 3) {
					dot.current[i].style.background = '#276CF2';
					dot.current[i].style.opacity = '1';	
				} else {
					dot.current[i].style.background = '#CFE3FF';
					dot.current[i].style.opacity = '0.2';
				}
			}				
			slider.current.style.transition = 'transform 0.5s ease-in-out';
			slider.current.style.transform = 'translateX(' + -counter + '00%)';				
			counter++;			
		}		
	}
	useEffect(() => {
		if (dot.current[0]) {
			dot.current[0].style.background = '#276CF2';
			dot.current[0].style.opacity = '1';
		}		
		setInterval(nextSlide, 3000);
	});	
		
	const check = () => {
		if (slider.current) {
			if (counter >= teacherSliderInfos.length + 1) {
				slider.current.style.transition = 'none';
				slider.current.style.transform = 'translateX(0)';
				counter = 1;									
			}
		}
	}
	return (		
		<div className='slider-container'>	
			<div className='slider-wrapper' ref={slider} onTransitionEnd={check}>
				{teacherSliderInfos.map((teacher, index) => (		
					<div className='slide-item' style={{background:`${bgSetting[index].background}`}}>					
						<div className='slide-inner-wrapper'>
							<div className='image-layer'>
								<figure className='quote-figure'>
									<img className='quote-image' src={quotes[index]} alt={'quotepic'+ index} id={'quote-pic' + index} />				
								</figure>
								<figure className='banner-figure'>
									<img className='banner-image' src={banners[index]} alt={'bannerpic'+ index} id={'bannerpic' + index} />					
								</figure>
							</div>
							<div className='word-layer'>
								<p className='slogon'>{teacher.slogon}</p>
								<p className='name'>{teacher.name}</p>
								<p className='title'>{teacher.title}</p>
							</div>							
						</div>											
					</div>									
				))}
				<div className='slide-item' style={{background:`${bgSetting[0].background}`}}>					
					<div className='slide-inner-wrapper'>
						<div className='image-layer'>
							<figure className='quote-figure'>
								<img className='quote-image' src={quotes[0]} alt='quotepic-last' id='quote-pic-last' />				
							</figure>
							<figure className='banner-figure'>
								<img className='banner-image' src={banners[0]} alt='bannerpic-last' id='bannerpic-last' />					
							</figure>
						</div>
						<div className='word-layer'>
							<p className='slogon'>{teacherSliderInfos[0].slogon}</p>
							<p className='name'>{teacherSliderInfos[0].name}</p>
							<p className='title'>{teacherSliderInfos[0].title}</p>
						</div>							
					</div>											
				</div>				
			</div>	
			<div className='dot-wrapper'>
				{teacherSliderInfos.map((url, index) => (
					<div className='pic-dot' ref={(el) => (dot.current[index] = el!)} />
				))}
			</div>			
		</div>			
	);
};

export default TeacherSlider;
