import React, { FC } from 'react';
import '../../../styles/about/TeacherCard.scss';
import AOS from 'aos';
import 'aos/dist/aos.css'; // You can also use <link> for styles
import { useAppData } from '../../../context/AppContext';
// ..
AOS.init();
const reqImages = require.context('../../../assets/aboutUs/teacher', true, /\.png$/);
const images = reqImages.keys().map((path) => reqImages(path));	


const TeacherCard: FC = () => {	
	const teacherCardInfos = useAppData('teacherCardInfos');	
	return (
		<div className='teachercard-container'>		
			<p className='best-team-header'>Our Best Team</p>
			<p className='best-team-content'>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua,Lorem ipsum dolor sit amet.</p>
			<div className='teachercard-wrapper'>
				{teacherCardInfos.map((teacher, index) => (		
					<div data-aos='fade-up' data-aos-delay={100 * (index % 2 + 1)} data-aos-duration="500" className='teachercard-item'>
						<figure className='teacher-fig'>
							<img id={'teacher-pic'+ index} src={images[index]} alt='teacher' />
						</figure>
						<p className='teacher-title'>{teacher.title}</p>
						<div className='div-line'></div>
						<p className='teacher-name'>{teacher.name}</p>
						<p className='teacher-description'>{teacher.slogon}</p>
					</div>																	
				))}
			</div>			
		</div>
	);
};

export default TeacherCard;
